<template >
  <div>
    <Navigator />
    <Propiedades/>
    <SectionBanner1/>    
    <SectionNosotros/>
    <SectionDirectorio/>
    <SectionServicios/>
    <SectionBanner2/>
    <SectionForm/>
    <Footer/>
      
  </div>
</template>
<script>

import {url} from '../global/index'; 
import Navigator from '../components/fixed/Navigator.vue'
import Propiedades from '../components/propiedades.vue';
import SectionBanner1 from '../components/SectionBanner1.vue';
import SectionNosotros from '../components/SectionNosotros.vue';
import SectionDirectorio from '../components/SectionDirectorio.vue';
import SectionServicios from '../components/SectionServicios.vue';
import SectionForm from '../components/SectionForm.vue';
import SectionBanner2 from '../components/SectionBanner2.vue';
import Footer from '../components/Footer.vue';
export default {
  name:'HOME',
  components: {
    Navigator,
    Propiedades,
    SectionBanner1,
    SectionNosotros,
    SectionDirectorio, 
    SectionServicios,
    SectionBanner2,
    SectionForm,
    Footer    

  },
  data(){
    return{
      url,
      name:'', 
      phone:'', 
      email:'', 
      mensaje:'',
      status:'',
      msg:'',
      aviso:false,
    }
  },
  created(){
    
  },
  methods: {
    enviarMail: async function (name, phone, email, mensaje) {
      this.msg = "";
      this.status = "";

      if (this.aviso == false) {
        this.msg = "Acepta los términos y condiciones.";
        this.status = "error";
      } else if (name == undefined || name == "" || name == null) {
        this.msg = "El campo nombre no es valido.";
        this.status = "error";
      } else if (phone == undefined || phone == "" || phone == null) {
        this.msg = "El campo medio no es valido.";
        this.status = "error";
      } else if (email == undefined || email == "" || email == null) {
        this.msg = "El campo correo no es valido.";
        this.status = "error";
      } else if (mensaje == undefined || mensaje == "" || mensaje == null) {
        this.msg = "El campo comentarios no es valido.";
        this.status = "error";
      } else {
        let data = {
          name: name,
          phone: phone,
          email: email, 
          mensaje: mensaje,
        };
        let response = await this.$store.dispatch(
          "admin/enviarMailContacto",
          data
        );
        console.log(response);
        if (response.status == "success") {
          this.status = "success";
          this.msg = "Mensaje enviado con exito.";
          this.name = "";
          (this.phone = ""),
            (this.email = ""), 
            (this.mensaje = "");
          this.delStatus();
        } else {
          this.status = "error";
          this.msg =
            "Lo sentimos ha ocurrido un error al intentar entregar tu mensaje.";
        }
      }
    },
  },
  
}
</script>
